@import url(https://fonts.googleapis.com/css?family=Roboto);
@import url(https://fonts.googleapis.com/css?family=Lora);
body {
  margin: 0;
  padding: 0;
  background-color: #f9f9f9;
  font: 10px 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased; }

h1, h2, h3, h4 {
  font-family: 'Lora', sans-serif;
  margin: 0; }

h1 {
  font-size: 3rem; }

h2 {
  font-size: 2.25rem; }

h3 {
  font-size: 1.75rem; }

h4 {
  font-size: 1.125rem; }

* {
  box-sizing: border-box; }

.container {
  width: 80%;
  margin: 0 auto; }

.font--bold {
  font-weight: bold; }

.Card__bannerImg {
  width: 20%;
  height: auto;
  margin-top: 5px; }
  @media (min-width: 600px) {
    .Card__bannerImg {
      margin-left: 20px; } }
  .Card__bannerImg img {
    width: 100%;
    height: auto; }

.CardTechlist__container {
  opacity: 0;
  transition: all 0.2s; }
  @media (min-width: 1200px) {
    .CardTechlist__container {
      position: absolute;
      top: 120px; } }

.CardTechlist {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  list-style: none;
  padding: 0;
  opacity: 0;
  margin: 5px 0 0;
  transition: opacity 2s;
  width: 100%; }
  @media (min-width: 1200px) {
    .CardTechlist {
      margin-top: 50px;
      justify-content: space-around; } }
  .js-expand-card .CardTechlist {
    opacity: 1; }

.CardTechlist .CardTechlist__item {
  padding: 5px; }
  @media (min-width: 1200px) {
    .CardTechlist .CardTechlist__item {
      padding: 10px; } }
  .CardTechlist .CardTechlist__item img {
    width: auto;
    height: 35px; }
    @media (min-width: 1200px) {
      .CardTechlist .CardTechlist__item img {
        height: 60px; } }

.Card__header {
  display: flex; }

.js-fadeIn {
  opacity: 1; }

.Card__stack {
  transition: all 0.3s;
  position: relative;
  z-index: 100;
  margin-top: -12px;
  cursor: pointer; }
  @media (min-width: 1200px) {
    .Card__stack:hover .Card__right {
      opacity: 1;
      margin-left: -50px; } }

@media (min-width: 1200px) {
  .TimelineCard__container:not(.js-expand-card) .Card__stack:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05); } }

@media (min-width: 1200px) {
  .TimelineCard__container:not(.js-expand-card) .Card__stack:hover .Card__right {
    -webkit-transform: rotate(2deg);
            transform: rotate(2deg);
    opacity: 1;
    top: 15px;
    right: -20px; } }

.Card {
  width: 100%;
  background-color: #FFF;
  border: 1px solid #e9e9e9;
  transition: all 0.3s;
  box-shadow: 0 20px 2px -10px rgba(0, 0, 0, 0.02); }
  @media (min-width: 1200px) {
    .Card {
      height: 200px; } }
  @media (min-width: 1200px) {
    .TimelineCard__container.js-expand-card .Card {
      width: 46%;
      height: 600px; } }
  @media (min-width: 1300px) {
    .TimelineCard__container.js-expand-card .Card {
      height: 550px; } }
  @media (min-width: 1400px) {
    .TimelineCard__container.js-expand-card .Card {
      height: 500px; } }

.Card__left {
  font-size: 1rem;
  transition: all 0.3s;
  position: relative;
  z-index: 1;
  padding: 25px 15px 20px 15px; }
  @media (min-width: 1200px) {
    .Card__container.js-expand-card .Card__left {
      -webkit-transform: rotate(-0.5deg);
              transform: rotate(-0.5deg);
      margin-left: -45px; } }

.Card__right {
  height: 0;
  opacity: 0;
  top: 0;
  right: 0; }
  @media (min-width: 1200px) {
    .Card__right {
      position: absolute;
      height: 100%;
      padding: 40px 50px; } }
  .TimelineCard__container.js-expand-card .Card__right {
    padding: 20px;
    opacity: 1;
    top: 15px;
    height: 100%; }
    @media (min-width: 1200px) {
      .TimelineCard__container.js-expand-card .Card__right {
        -webkit-transform: rotate(2deg);
                transform: rotate(2deg);
        margin-right: 65px;
        box-shadow: 0 20px 2px -10px rgba(0, 0, 0, 0.02);
        padding: 30px 20px; } }

.Card__description {
  font-size: 1rem;
  line-height: 1.5rem;
  transition-property: width, height, opacity;
  transition-duration: 0.5s, 0.5s, 0.5s;
  text-align: justify;
  overflow: hidden;
  opacity: 0;
  height: 0;
  margin: 0; }
  .js-expand-card .Card__description {
    opacity: 1;
    height: 100%; }

.Card__details {
  float: left;
  width: 65%;
  margin-left: 20px; }
  @media (min-width: 1200px) {
    .Card__details {
      width: 55%; } }

.Card__title {
  margin-bottom: 10px;
  font-size: 1.5rem;
  font-weight: 100; }
  @media (min-width: 600px) {
    .Card__title {
      font-size: 1.75rem;
      line-height: 1.75rem;
      width: 200px; } }

.Card__locations {
  color: lightgray;
  margin: 0;
  padding: 0;
  list-style: none; }

.Card__dates {
  margin-bottom: 10px; }

.Card__joiner {
  width: 7px;
  height: 100px;
  background-color: #000;
  margin: -15px auto 0;
  opacity: 0;
  transition: all 1s; }

.Card__joiner-date {
  font-size: 1rem;
  margin-left: 25px;
  width: -webkit-max-content;
  width: max-content;
  padding: 40px 10px;
  position: relative; }
  .Card__joiner-date:before {
    content: '';
    height: 3px;
    width: 30px;
    background-color: #000;
    display: block;
    position: absolute;
    margin-top: 7px;
    left: -25px; }
  .Card__joiner-date:after {
    content: '';
    height: 18px;
    width: 18px;
    display: inline-block;
    background-color: #000;
    border-radius: 15px;
    position: absolute;
    left: -30px; }

.TimelineCard__container {
  position: relative;
  transition: all 0.3s;
  margin: 0 auto;
  opacity: 0;
  width: 90%; }
  @media (min-width: 1200px) {
    .TimelineCard__container {
      width: 50%; } }
  @media (min-width: 1200px) {
    .TimelineCard__container.js-expand-card {
      width: 100%; } }
  .TimelineCard__container.js-show-card {
    opacity: 1; }
    .TimelineCard__container.js-show-card .Card__joiner {
      opacity: 1; }

@media (min-width: 900px) {
  .Timeline {
    padding-top: 100px;
    margin-left: 48%;
    width: 100%; } }

.Menu {
  text-align: center;
  padding: 40px 50px 10px; }
  @media (min-width: 1300px) {
    .Menu {
      display: flex;
      justify-content: space-between;
      padding: 70px 50px 10px 70px;
      align-items: center; } }

.Menu__title {
  font-size: 3rem;
  line-height: 3rem; }
  @media (min-width: 1300px) {
    .Menu__title {
      font-size: 2.5rem;
      line-height: inherit; } }

.Menu__list {
  margin: 25px; }
  .Menu__list a {
    text-decoration: none;
    padding: 6px 20px;
    font-size: 1rem;
    border: 1px solid #003c50;
    margin: 0 7px;
    color: #003c50; }

.Intro {
  padding: 10px 5vw;
  background-color: #add8e6; }
  @media (min-width: 600px) {
    .Intro {
      padding: 20px 50px 40px 70px; } }

.Intro__list {
  margin: 30px 20px;
  padding: 0;
  list-style: none; }

.Intro__heading {
  text-align: center;
  font-size: 1.3rem; }
  @media (min-width: 1200px) {
    .Intro__heading {
      font-size: 1.75rem; } }
  @media (min-width: 900px) {
    .Intro__heading {
      text-align: inherit; } }

.Intro__list-item {
  font-size: 1rem;
  position: relative;
  margin-top: 10px; }
  @media (min-width: 1200px) {
    .Intro__list-item {
      font-size: 1.2rem; } }
  .Intro__list-item:before {
    content: '';
    position: absolute;
    border-right: 2px solid black;
    border-bottom: 2px solid black;
    width: 7px;
    height: 7px;
    top: 11px;
    left: -20px;
    -webkit-transform: translateY(-50%) rotate(-45deg);
            transform: translateY(-50%) rotate(-45deg); }

.Sidebar {
  background-color: lightblue;
  width: 100%; }
  @media (min-width: 900px) {
    .Sidebar {
      width: 45%;
      height: 100%;
      position: fixed;
      overflow: scroll; } }

.Loading {
  width: 100px;
  height: 10px;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-size: 1rem; }

.App {
  width: 100%; }
  @media (min-width: 900px) {
    .App {
      display: flex; } }

